.content {
    padding: 2rem;
    height: 90%;    /*100퍼로 하면 아래 내려감. 확인예쩡*/
    background-color: rgb(243, 224, 252);
}

.content > h1 {
    color: rgb(169, 29, 235);
    margin-bottom: 0.5rem;
}

.grid-container {
    display: grid;
    height: 80%;            /*100퍼로 하면 아래 내려감. 확인예쩡*/
    /* border: 1px solid rgb(204, 24, 93); */
    box-sizing: inherit;    /*부모 기준으로 크기 정함*/
    gap: 1rem;
}

.grid-item {
    background-color: rgb(255, 255, 255);
    border-radius: 1rem;
    padding: 1rem;
}

.grid-item > h4 {
    color: rgb(102, 15, 143);
    margin-bottom: 0.5rem;
}
/* ---- map content ---- */
#map > .grid-container {
    grid-template-columns: 3fr 1fr;
    grid-template-rows: 1fr 4fr;
}

#map #item-map {
    grid-column: 1/2;
    grid-row: 1/3;
}


/* ---- pickup content ---- */
.pickup-item {
    overflow: hidden; /*부모 높이를 잊어벼리기때문에 다시 높이를 생기게끔해주는 float해재 기법*/
    padding: 1rem 2rem;
}

.pickup-item > *{
    float: left; /*가로 정렬을 하기위한 float*/
    /* padding: 0rem 1rem; */
}
.pickup-item > .pickup-number{
    width: 2rem;
    font-size: 2rem;
}
.pickup-store {
    width: 5rem;
}
/* ---- user content ---- */
#user > .grid-container {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 2fr;
}

#user #item-profile {
    grid-column: 1/4;
    grid-row: 1;
}

#user #item-profile > img{
    width: 3rem;
}
/* ---- call content ---- */
#call > .grid-container {
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr 3fr;
}

#call #item-button {
    grid-column: 1/2;
    grid-row: 1/3;
}

#call #item-button > #call-button {
    background-color: rgb(209, 215, 241);
    font-size: 3rem;
    width: 20rem;
    height: 10rem;
    border: 1px solid rgb(102, 15, 143);
    
    /* padding: 1rem 3rem; */
}

/* ---- login content ---- */

#login > .grid-container{
    grid-template-columns: 1fr 2fr 1fr;
    /* border: 1px solid rgb(235, 179, 75); */

}

#login #item-form {
    grid-column: 2/3;
    display: block;
    padding: 3rem 3rem;

}

/* ---- side-nav content ---- */
#side-nav {
    border: 1px solid rgb(221, 106, 250);
    height:100%;
    background-color: rgb(209, 215, 241);
    /* transition: 0.8s ease; */
}

#side-nav > #title {
    margin-top: 1rem;
    margin-bottom: 1rem;
}

#side-nav  img {
    width: 1rem;
}

#side-nav > .nav-item{
    border: 1px solid rgb(84, 165, 241);
    padding-top: 1rem;
    padding-bottom: 1rem;
    width: 100%;
    /* padding: 1rem 3rem; */
    width: auto;
    display: block;
    
}

#side-nav > .nav-active{
    background-color: rgb(240, 213, 255);
    border-left: 3px solid rgb(141, 11, 6); 
}

#side-nav > #nav-user {
    margin-right: auto;
    margin-left: auto;
    border: 1px solid rgb(115, 166, 243);
    padding: 1rem 1rem;
    /* width: 8rem; */
    float: center;
    display: block;
    bottom: 2rem;
    position: fixed;
}

#side-nav > #nav-login {
    margin-right: auto;
    margin-left: auto;
    border: 1px solid rgb(115, 166, 243);
    padding: 1rem 3rem;
    float: center;
    display: block;
    bottom: 2rem;
    position: fixed;
}
